const questionItems = document.querySelectorAll('.question-list-item');

questionItems.forEach(item => {
  const header = item.querySelector('.question-header');

  header.addEventListener('click', () => {
    const isActive = item.classList.contains('active');

    // –ó–∞–∫—Ä—ã–≤–∞–µ–º –≤—Å–µ –≤–æ–ø—Ä–æ—Å—ã
    questionItems.forEach(el => {
      el.classList.remove('active');

      const icon = el.querySelector('.question-icon');
      if (icon) {
        icon.src = 'img/plus.webp'; // ‚Üê –¢–ê–ö (–±–µ–∑ —Ç–æ—á–∫–∏ –∏ —Å–ª–µ—à–∞)
      }
    });

    // –ï—Å–ª–∏ –∫–ª–∏–∫–Ω—É–ª–∏ –ø–æ –∑–∞–∫—Ä—ã—Ç–æ–º—É ‚Äî –æ—Ç–∫—Ä—ã–≤–∞–µ–º
    if (!isActive) {
      item.classList.add('active');

      const icon = item.querySelector('.question-icon');
      if (icon) {
        icon.src = 'img/minus.webp'; // ‚Üê –¢–ê–ö
      }
    }
  });
});

// –ù–∞—á–∞–ª—å–Ω–æ–µ —Å–æ—Å—Ç–æ—è–Ω–∏–µ ‚Äî –≤—Å–µ –∑–∞–∫—Ä—ã—Ç—ã –∏ –ø–ª—é—Å–∏–∫–∏
questionItems.forEach(el => {
  el.classList.remove('active');

  const icon = el.querySelector('.question-icon');
  if (icon) {
    icon.src = 'img/plus.webp'; // ‚Üê –¢–ê–ö
  }
});
